  flex-flow: row nowrap;
  align-items: center;
  transition-delay: 0s, 0.6s; }
  .header.fixed {
    padding: 7px 0; }
  .header.show-menu {
    background-color: transparent;
    transition: padding 0.2s; }
    .header.show-menu .header__phone, .header.show-menu .header__sublink {
      opacity: 0; }
    .header.show-menu .logo {
      opacity: 0; }
  .header__phone {
    display: block;
    width: auto;
    font-family: "Open Sans";
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-decoration: none;
    color: #561f1b;
    transition: opacity 0.4s; }
  .header__sublink {
    display: inline-flex;
