  .header__sublink-svg {
    margin-right: 3px;
    flex: 0 0 14px;
    width: 14px;
    height: 14px; }
    .header__sublink-svg svg {
      width: 100%;
      height: 100%; }
  .header__burger {
    position: relative;
    top: 6px;
    display: block;
    border: none;
    padding: 0;
    width: 25px;
    height: 18px;
    background: transparent;
    transform: translateY(-50%); }
    .header__burger::before, .header__burger::after {
      content: "";
      position: absolute;
      left: 0;
      display: block;
      width: 25px;
      height: 2px;
      background-color: #ff4200;
      transform-origin: center center;
      transition: transform 0.4s, top 0.4s, left 0.4s, background-color 0.4s; }
    .header__burger::before {
      top: 0; }
    .header__burger::after {
      top: 100%;
      transform: translateY(-100%); }
    .header__burger span {
      position: absolute;
      left: 0;
      right: 0;
      top: 50%;
      display: block;
      height: 2px;
      background-color: #ff4200;
      opacity: 1;
      transform: translateY(-50%);
      transition: opacity 0.4s, background-color 0.4s; }
    .header__burger.active::before, .header__burger.active::after {
      left: 50%;
      top: 50%;
      background-color: #fff;
      transition-delay: 0s; }
    .header__burger.active::before {
      transform: translate(-50%, -50%) rotate(-45deg); }
    .header__burger.active::after {
      transform: translate(-50%, -50%) rotate(45deg); }
    .header__burger.active span {
      opacity: 0;
      background-color: #fff; }

